import React, {useState} from 'react'
import './MainIcons.css'
import '../../../node_modules/hover.css/css/hover.css'

import Grid from 'rsuite/Grid';
import Row from 'rsuite/Row';
import Col from 'rsuite/Col';
import MapModal from '../Modals/MapModal/MapModal'
import { Link } from 'react-router-dom';

function MainIcons() {
    const [toggle, setToggle] = useState("")
    const [modalShow, setModalShow] = React.useState(false);


    const toggleClass = (element) => {
        switch(element) {
            case "rodjendan":
                setToggle("rodjendan");
                break;
            case "edukacija": 
                setToggle("edukacija");
                break;
            case "kupovina": 
                setToggle("kupovina");
                break;
            case "sport": 
                setToggle("sport");
                break;
            case "zdravstvo": 
                setToggle("zdravstvo");
                break;

        }
            
    }

    return (
        <>
        <Grid fluid className={` ${toggle != "" ? toggle : ""} categoryGrid`}>

            <Row className="show-grid">
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={8} lg={4} className="oneCol mainCategory rodjendanBack hvr-float" onMouseEnter={() => toggleClass("rodjendan")}>
                    {/* <img src={bed} className="iconGrid px-2"/> */}
                    <p className="gridText">Rodjendan</p>
                </Col>
            </Row> 

            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "rodjendan" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/11`} state={{categoryName: "Prostori za rodjendan"}} className='rodjendan'>Prostor</Link></div></Col>
                <Col xs={8} lg={4} className="oneCol mainCategory edukacBack hvr-float" onMouseEnter={() => toggleClass("edukacija")}>
                    <p className="gridText">Edukacija</p>
                </Col>

            </Row> 


            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "rodjendan" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/12`} state={{categoryName: "Muzika za rodjendan"}} className='rodjendan'>Muzika</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/21`} state={{categoryName: "VrtiÄi"}} className='edukacija'>VrtiÄ</Link></div></Col>
                <Col xs={8} lg={4} className="oneCol mainCategory kupovinaBack  hvr-float" onMouseEnter={() => toggleClass("kupovina")}>
                    <p className="gridText">Kupovina</p>
                </Col>

            </Row> 

            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "rodjendan" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/13`} state={{categoryName: "Fotografi za rodjendan"}} className='rodjendan'>Fotograf</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/22`} state={{categoryName: "Å kole"}} className='edukacija'>Å kola</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "kupovina" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/31`} state={{categoryName: "IgraÄke"}} className='kupovina'>IgraÄke</Link></div></Col>
                
                <Col xs={8} lg={4} className="oneCol mainCategory sportBack  hvr-float" onMouseEnter={() => toggleClass("sport")}>
                    <p className="gridText">Sport</p>
                </Col>
            </Row> 

            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "rodjendan" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/14`} state={{categoryName: "Hrana za rodjendan"}} className='rodjendan'>Hrana</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/23`} state={{categoryName: "Strani jezik"}} className='edukacija'>Strani jezik</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "kupovina" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/32`} state={{categoryName: "Shoping malovi"}} className='kupovina'>Shoping mal</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "sport" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/41`} state={{categoryName: "Bazeni"}} className='sport'>Bazeni</Link></div></Col>
                <Col xs={7} lg={4}className="oneCol mainCategory zdravstvoBack hvr-float " onMouseEnter={() => toggleClass("zdravstvo")}>
                    <p className="gridText">Zdravstvo</p>
                </Col>

            </Row> 

            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "rodjendan" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/15`} state={{categoryName: "PoslastiÄarnice"}} className='rodjendan'>PoslastiÄarnice</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/24`} state={{categoryName: "Muzika"}} className='edukacija'>Muzika</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "kupovina" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/33`} state={{categoryName: "Garderoba"}} className='kupovina'>Garderoba</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "sport" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/42`} state={{categoryName: "Å kole sporta"}} className='sport'>Å kole sporta</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "zdravstvo" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/51`} state={{categoryName: "Privatne klinike"}} className='zdravstvo'>Privatne klinike</Link></div></Col>
            </Row>

            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/25`} state={{categoryName: "Umetnost"}} className='edukacija'>Umetnost</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "kupovina" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/34`} state={{categoryName: "ObuÄa"}} className='kupovina'>ObuÄa</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "sport" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/43`} state={{categoryName: "Odbojka"}} className='sport'>Odbojka</Link></div></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "zdravstvo" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/52`} state={{categoryName: "Apoteke"}} className='zdravstvo'>Apoteke</Link></div></Col>
            </Row>
            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/26`} state={{categoryName: "Priprema za Å¡kole"}} className='edukacija'>Priprema za Å¡kole</Link></div></Col>
                <Col xs={4} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "sport" ? 'visible' : "invisible"} subCategory `}><Link to={`/catg/44`} state={{categoryName: "KoÅ¡arka"}} className='sport'>KoÅ¡arka</Link></div></Col>
            </Row>
            <Row className="show-grid"> 
                <Col xs={0} lg={2} className="oneCol"></Col>
                <Col xs={4} className="oneCol"></Col>
                <Col xs={4} className="oneCol"><div className={`${toggle == "edukacija" ? 'visible' : "invisible"} subCategory`}><Link to={`/catg/27`} state={{categoryName: "Priprema za vrtiÄe"}} className='edukacija'>Priprema za vrtiÄe</Link></div></Col>
            </Row>


        </Grid>







        {/* <MapModal
                    show={modalShow}
                    onHide={() => setModalShow(false)}
                /> */}
        </>
    )
}

export default MainIcons
